//
// 버튼 스타일
// --------------------------------------------------

.btn {
	box-sizing: border-box;
	display: inline-block;
	height: 34px;
	line-height: 34px;
	vertical-align: middle;
	padding: 0 20px;
	text-align: center;
	font-size: 14px;
	color: #fff;
	text-decoration: none;
	white-space: nowrap;
	background: #707070;

	&:hover,
	&:focus {
		text-decoration: none;
	}
	&:active {
		box-shadow: inset 0 3px 3px rgba(0,0,0,.2);
		transition: box-shadow .1s;
	}

	/* 크기 */
	&.big { height: 40px; line-height: 40px; }
	&.full { display: block; width: 100%; padding: 0; text-align: center; }

	/* 색상타입 */
	&.primary { background: #000; }
	&.white { line-height: 32px; color: #505050; background: #fff; border: 1px solid #d0d0d0; }
}

/* 좋아요 버튼 */
.btn-like {
	box-sizing: border-box;
	display: inline-block;
	height: 34px;
	line-height: 34px;
	vertical-align: middle;
	padding: 0 10px 0 28px;
	text-align: center;
	font-size: 14px;
	color: #505050;
	white-space: nowrap;
	background: #fff url('../img/com/ico-like-off.svg') no-repeat 10px 11px;
	background-size: 14px 12px;
	border: 1px solid #d0d0d0;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	&.on {
		background-image: url('../img/com/ico-like-on.svg');
	}
}

