	background: #fff url('../img/com/ico-comment.svg') no-repeat 8px 10px;
	background-size: 16px 14px;
	border: 1px solid #d0d0d0;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include mob {
		display: inline-block;
	}
}

/* 리스트 담기 버튼 */
.btn-list-put {
	display: inline-block;
	width: 24px;
	height: 0;
	max-height: 20px;
	padding-top: 20px;
	vertical-align: middle;
	text-decoration: none;
	background: url('../img/com/ico-put.svg') no-repeat;
	background-size: cover;
	overflow: hidden;
}

