//
// input 스타일
// --------------------------------------------------

/* 텍스트 인풋 */
.basic-txt {
	box-sizing: border-box;
	display: inline-block;
	height: 34px;
	line-height: 32px;
	padding: 0 10px;
	font-size: 14px;
	color: #666;
	background: #fff;
	border: 1px solid #d0d0d0;
	border-radius: 0;

	&::-ms-clear { display: none; }
	&::-webkit-input-placeholder { color: #888; }
	&:-ms-input-placeholder { color: #888; }
	&::-moz-placeholder { color: #888; opacity: 1; }
	&:-moz-placeholder { color: #888; opacity: 1; }

	&.full { display: block; width: 100%; }
}

/* 텍스트 인풋 - textaera type */
textarea.basic-txt {
	height: auto;
	padding: 10px;
	line-height: 1.5;
	resize: none;
}

/* 셀렉트박스 */
.basic-select {
	box-sizing: border-box;
	display: inline-block;
	height: 34px;
	line-height: 32px;
	padding: 0 0 0 10px;
	color: #666;
	background: #fff;
	border: 1px solid #d0d0d0;
	border-radius: 0;
	-webkit-appearance: none;

	/* IE가 아닐때만 */
	.noIE & {
		padding: 0 30px 0 10px;
		background:#fff url('../img/com/ico-select-arw.png') no-repeat calc(100% - 14px) 50%; 
		background-size: 10px 7px;
	}

	&.full { display: block; width: 100%; }
}

/* 체크박스 */
input[type="checkbox"].basic-chk {
	display: inline-block;
	width: 15px;
	height: 15px;
	margin: 0;
	padding: 0;
	vertical-align: middle;

	html:not(.FF) & {
		border: 0 none;
		border-radius: 0;
		-webkit-appearance: none;
		background:url('../img/com/bg-chk-off.png') no-repeat; 

		&:checked { background:url('../img/com/bg-chk-on.png') no-repeat;  }

		& + label { margin-left: 5px; vertical-align: middle; }
	}
}

