/* 프로필보기 페이지 */
.profile-page {
	.common-page-title {
		position: absolute;
		left: -9999px;
	}

	.common-content {
		margin-top: 0;
	}

	@include mob {
		.common-content { margin-top: 4%; }
	}
}

/* 프로필 박스 */
.profile-box {
	margin-bottom: 30px;
	padding: 35px 40px;
	background: #f4f4f4;

	@include clearfix;

	.info {
		box-sizing: border-box;
		float: left;
		width: calc(100% - 240px);
		min-height: 130px;
		padding-right: 40px;
		padding-left: 165px;
		border-right: 1px solid #d0d0d0;
		position: relative;
	}

	.avatar {
		position: absolute;
		top: 0;
		left: 0;
	}

	.nickname {
		font-weight: bold;
		font-size: 26px;
		color: #000;
	}

	.email {
		font-weight: 200;
		font-size: 16px;
		color: #888;
	}

	.greeting {
		margin-top: 13px;
		font-weight: 200;
		font-size: 14px;
		color: #505050;
	}

	.stat {
		box-sizing: border-box;
		float: right;
		width: 200px;
	}

	.stat-list {
		height: 50px;
		padding-top: 15px;
		text-align: justify;

		li {
			display: inline-block;
			text-align: center;
			font-size: 16px;
		}

		strong {
			line-height: 1.3;
			font-size: 18px;
			color: #000;
		}

		&:after {
			content: '';
			display: inline-block;
			width: 100%;
		}
	}

	.btn-action {
		display: block;
		height: 38px;
		line-height: 38px;
		margin-top: 15px;
		text-align: center;
		font-size: 14px;
		color: #505050;
		text-decoration: none;
		background: #fff;
		border: 1px solid #d0d0d0;

		i.edit {
			display: inline-block;
			width: 18px;
			height: 22px;
			margin-right: 2px;
			vertical-align: middle;
			background: url('../img/com/ico-edit.svg') no-repeat;
			background-size: cover;
		}

		i.follow {
			display: inline-block;
			width: 25px;
			height: 24px;
			margin-right: 2px;
			vertical-align: middle;
			background: url('../img/com/ico-follow.svg') no-repeat;
			background-size: cover;
		}

		i.unfollow {
			display: inline-block;
			width: 25px;
			height: 24px;
			margin-right: 2px;
			vertical-align: middle;
			background: url('../img/com/ico-unfollow.svg') no-repeat;
			background-size: cover;
		}
	}

	@include mob {
		padding: 30px;

		.info {
			float: none;
			width: auto;
			padding-right: 0;
			padding-left: 0;
			padding-bottom: 20px;
			text-align: center;
			border-right: 0 none;
			border-bottom: 1px solid #d0d0d0;
		}

		.avatar {
			position: static;
			margin-bottom: 10px;
		}

		.stat {
			float: none;
			margin: 0 auto;
			padding-left: 0;
		}
	}
}

/* 마이페이지 - 정보수정 */
.my-page-edit {
	.additional {
		margin-top: 40px;

		.title {
			display: inline;
			margin-right: 10px;
			vertical-align: middle;
			font-weight: bold;
			font-size: 26px;
			color: #000;
		}

		.desc {
			display: none;
			padding-bottom: 20px;
			padding-left: 10px;
			text-indent: -10px;
			font-weight: 200;
			font-size: 14px;
			color: #888;
		}

		form { display: none; }

		&.on {
			.desc { display: block; }
			form { display: block; }
		}
	}

	@include mob {
		.additional {
			.title {
				font-size: 22px;
			}

			.desc {
				padding-bottom: 10px;
			}
		}
	}
}

