//
// 상세보기
// --------------------------------------------------

/* 상세보기 */
.detail-view {
	width: $content-width;
	margin: 0 auto;

	@include tab {
		padding: 0 4% 8%;
	}

	@include mob {
		padding: 4% 4% 8%;
	}

	.view-container {
		position: relative;

		@include clearfix;
	}

	.player-area {
		float: left;
		width: 70%;

		.player-wrap {
			width: 100%;
			padding-top: 56.25%;
			background: #000;
			position: relative;

			> iframe {
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
			}
		}
	}

	.btn-download {
		display: block;
		width: 88px;
		height: 0;
		max-height: 88px;
		padding-top: 88px;
		background: url('../img/com/ico-download.svg') no-repeat;
		background-size: cover;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		overflow: hidden;
	}

	.detail-info {
		clear: both;
		padding: 25px 0 0;
		font-size: 14px;
		color: #707070;

		.section-title {
			@include hidden;
		}

		.info-header {
			@include clearfix;
		}

		.title {
			float: left;
			width: calc(100% - 284px);
			font-weight: bold;
			font-size: 18px;
			color: #000;
			word-wrap: break-word;
		}

		.info {
			float: right;
			width: 284px;
			text-align: right;
		}

		.author {
			float: left;
		}

		.action {
			clear: both;
			padding-top: 10px;
		}
		
		.content-txt {
			padding: 20px 0 35px;
			font-weight: 200;
			word-wrap: break-word;
		}
	}

	.side-area {
		width: 28.4%;
		padding-top: 39.37%;
		position: absolute;
		top: 0;
		right: 0;
	}
	
	.related-list {
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;

		.tab {
			@include clearfix;

			li {
				float: left;
				box-sizing: border-box;
				width: 50%;
				height: 40px;
				line-height: 38px;
				text-align: center;
				font-size: 16px;
				color: #fff;
				background: #707070;
				border: 1px solid #707070;
				border-bottom: 0 none;

				&.on {
					color: $main-color;
					background: #fff;
					border-color: #d0d0d0;
				}
			}

			a {
				display: block;
				text-decoration: none;
			}
		}

		.tab-con {
			box-sizing: border-box;
			height: calc(100% - 40px);
			border: 1px solid #d0d0d0;
			border-top: 0 none;
			overflow: scroll;
			overflow-x: hidden;
			-webkit-overflow-scrolling: touch;
		}

		.list-more {
			box-sizing: border-box;
			display: block;
			width: 100%;
			padding: 20px 0;
			text-align: center;
			font-size: 0;
			color: transparent;
			background: url('../img/com/ico-list-more.png') no-repeat 50% 50%;
			background-size: 14px 10px;
			border-top: 1px solid #d0d0d0;

			&:hover,
			&:focus {
				background-color: #fafafa;
			}
		}

		.tab-title {
			@include hidden;
		}
	}

	.comment {
		padding-top: 35px;
		border-top: 1px solid #d0d0d0;
	}

	@include tab {
		width: auto;

		.player-area {
			width: 63%;
		}

		.side-area {
			width: 35.4%;
			padding-top: 35.45%;
			position: absolute;
			top: 0;
			right: 0;
		}
	}

	@include mob {
		.player-area {
			float: none;
			width: auto;
		}

		.btn-download {
			width: 60px;
			max-height: 60px;
			padding-top: 60px;
		}

		.detail-info {
			padding-top: 4%;

			.info {
				float: none;
				width: auto;
				text-align: left;
			}

			.author {
				padding-top: 5px;
				float: none;
			}
			
			.content-txt {
				padding: 4% 0 8%;
			}
		}

		.side-area {
			width: auto;
			padding-top: 0;
			position: relative;
		}
	
		.related-list {
			width: auto;
			height: auto;
			position: static;

			.tab-con {
				height: auto;
				border: 0 none;
				overflow: auto;
			}

			.list-more {
				padding: 4% 0;
			}
		}

		.comment {
			padding-top: 8%;
			border-top: 0 none;
		}
	}
}