//
// 콘텐츠 리스트 - 작은 섬네일
// --------------------------------------------------

/* 콘텐츠 리스트 - 작은 섬네일 */
.small-content-list {
	padding: 15px 10px;

	> li {
		&:not(:first-child) {
			margin-top: 15px;
		}

		> a {
			display: block;
			line-height: 18px;
			font-size: 14px;
			color: #888;
			position: relative;
			@include clearfix;
		}
	}

	.thumb {
		float: left;
		width: 82px;
		height: 82px;
		background: #000;
		margin-right: 10px;
		position: relative;
		overflow: hidden;

		img {
			height: 100%;
			position: relative;
			left: 50%;
			transform: translate(-50%,0);
		}
	}

	.info-box {
		padding-top: 4px;
		padding-left: 92px;
	}

	.title {
		max-height: 36px;
		font-weight: bold;
		color: #000;
		word-wrap: break-word;
		overflow: hidden;
	}

	.info {
		margin-top: 5px;
		font-weight: 200;
	}

	@include tab {

		.info-box {
			padding-top: 2px;
			padding-left: 80px;
		}
		.thumb {
			width: 70px;
			height: 70px;
		}
	}
}