//
// 푸터 스타일
// --------------------------------------------------

/* 공통 푸터 */
.common-footer {
	box-sizing: border-box;
	width: 100%;
	height: 85px;
	line-height: 18px;
	color: #afafaf;
	background: #505050;
	position: absolute;
	bottom: 0;
	left: 0;
	
	.content-wrap {
		max-width: 1040px;
		margin: 0 auto;
		padding: 15px 0 0;
		position: relative;
	}

	h2 {
		@include hidden;
	}

	.nav {
		@include clearfix;

		li {
			float: left;
			position: relative;

			&:not(:first-child) {
				margin-left: 7px;
				padding-left: 7px;

				&:after {
					content: '';
					display: block;
					width: 1px;
					height: 12px;
					background: #afafaf;
					position: absolute;
					top: 3px;
					left: 0;
				}
			}
		}

		a:hover,
		a:focus {
			color: #ddd;
		}
	}

	address {
		font-style: normal;
	}

	.btn-top {
		position: absolute;
		top: 15px;
		right: 0;

		img {
			vertical-align: top;
		}
	}

	.btn-add {
		position: fixed;
		bottom: 40px;
		right: 2.5%;
		z-index: 900;

		img {
			width: 70px;
			height: 70px;
			vertical-align: top;
		}
	}

	@include pc-large {
		padding-left: 190px;
	}

	@include tab {
		padding: 0 8%;

		.btn-add {
			bottom: 4%;
			right: 4%;

			img {
				width: 55px;
				height: 55px;
			}
		}
		
		.btn-top { display: none; }
	}

	@include mob {
		.nav { display: none; }
		.addr { display: none; }
		.cs:before { content:"\a"; white-space:pre; }
	}
}