/* ⓝ chart, Hot ⓝple */
.list-chart {
	.rank-page {
		text-align: center;
		margin-top: 30px;

		li { width: 176px; margin-bottom: 0; }
		li:not(:first-child) { margin-left: 7px; }

		@include mob {
			margin-top: 4%;
			li { width: 110px; }
		}
	}
}

.srch-result-txt {
	margin-bottom: 30px;
	padding: 17px;
	text-align: center;
	font-size: 18px;
	color: #000;
	background: #f4f4f4;

	strong {
		font-weight: bold;
		font-size: 26px;
	}

	span {
		color: #ff0000;
	}

	@include mob {
		strong {
			font-size: 22px;
		}
	}
}