//
// 팝업 페이지
// --------------------------------------------------

/* 팝업 - 아이디 찾기 */
.pop-find-idpw {
	.pop-header {
		text-align: center;

		h1 {
			font-size: 26px;
			color: #000;
		}

		.desc {
			font-weight: 200;
			font-size: 14px;
			color: #505050;
		}
	}

	.body { padding: 4%; }

	.btn-submit {
		margin: 7px 0 0;
		display: block;
	}
}

/* 팝업 - follow list */
.pop-follow-list {
	.pop-header {
		text-align: center;

		h1 {
			font-size: 26px;
			color: #000;
		}
	}

	.list {
		box-sizing: border-box;
		height: 260px;
		margin-top: 15px;
		padding: 20px 0;
		border-top: 1px solid #d0d0d0;
		border-bottom: 1px solid #d0d0d0;
		overflow: scroll;
		overflow-x: hidden;

		> li {
			height: 34px;
			line-height: 34px;
			position: relative;

			&:not(:first-child) {
				margin-top: 15px;
			}
		}

		a {
			text-decoration: none;
		}
	}

	.name {
		display: inline-block;
		max-width: calc(100% - 170px);
		vertical-align: middle;
		font-weight: 200;
		font-size: 14px;
		color: #505050;

		@include ellipsis;
	}

	.btn-action {
		display: block;
		width: 102px;
		height: 32px;
		line-height: 32px;
		text-align: center;
		font-size: 14px;
		color: #505050;
		text-decoration: none;
		background: #fff;
		border: 1px solid #d0d0d0;
		position: absolute;
		top: 0;
		right: 15px;

		i.follow {
			display: inline-block;
			width: 21px;
			height: 20px;
			margin-right: 2px;
			vertical-align: middle;
			background: url('../img/com/ico-follow.svg') no-repeat;
			background-size: cover;
		}

		i.unfollow {
			display: inline-block;
			width: 21px;
			height: 20px;
			margin-right: 2px;
			vertical-align: middle;
			background: url('../img/com/ico-unfollow.svg') no-repeat;
			background-size: cover;
		}
	}

	@include mob {
		.name {
			max-width: calc(100% - 145px);
		}

		.btn-action {
			width: 80px;

			i.follow,
			i.unfollow { display: none; }
		}
	}
}