//
// 페이징
// --------------------------------------------------

/* 페이징 - 공통 */
.paging-common {
	margin-top: 30px;
	text-align: center;
	font-size: 0;

	a {
		box-sizing: border-box;
		display: inline-block;
		height: 34px;
		line-height: 34px;
		vertical-align: top;
		text-decoration: none;
	}

	.paging-num {
		display: inline-block;
		vertical-align: top;

		a {
			padding: 0 12px;
			font-size: 16px;
			color: #707070;
			background: #fff;
			border: 1px solid #d0d0d0;

			&:not(:first-child) {
				margin-left: 5px;
			}
			
			&:hover,
			&:focus {
				background: #fafafa;
			}

			&.on {
				color: #fff;
				background: $main-color;
				border-color: $main-color;
			
				&:hover,
				&:focus {
					opacity: .8;
				}
			}
		}
	}

	.page-btn {
		width: 34px;
		color: transparent;
		background-color: #fff;
		background-position: center;
		background-repeat: no-repeat;
		border: 1px solid #d0d0d0;
			
		&:hover,
		&:focus {
			background-color: #fafafa;
		}

		&.first {
			margin-right: 5px;
			background-image: url('../img/com/ico-arw-first.png');
		}

		&.prev {
			margin-right: 15px;
			background-image: url('../img/com/ico-arw-prev.png');
		}

		&.next {
			margin-left: 15px;
			background-image: url('../img/com/ico-arw-next.png');
		}

		&.last {
			margin-left: 5px;
			background-image: url('../img/com/ico-arw-last.png');
		}
	}

	@include mob-large {
		margin-top: 8%;
		margin-left: -4%;
		margin-right: -4%;

		.page-btn {
			&.first { display: none; }
			&.last { display: none; }
		}
	}

	@include mob-small {
		.paging-num a {
			height: 30px;
			line-height: 30px;
			padding: 0 10px;

			&:not(:first-child) { margin-left: 2px; }
		}
		.page-btn {
			width: 30px;
			height: 30px;
			line-height: 30px;

			&.prev { margin-right: 5px; }
			&.next { margin-left: 5px; }
		}
	}
}


/* 페이징 - 도트타입 */
.paging-dot {
	margin-top: 25px;
	text-align: center;
	font-size: 0;

	> a {
		display: inline-block;
		width: 12px;
		height: 12px;
		vertical-align: top;
		color: transparent;
		background: #afafaf;
		border-radius: 6px;

		&:not(:first-child) {
			margin-left: 17px;
		}

		&.on {
			background: #000;
		}
	}
}