//
// form 스타일
// --------------------------------------------------

/* 기본 form */
.frm-basic-row {
	padding-bottom: 20px;
	font-size: 0;

	input[type=file] {
		height: 34px;
	}

	> dt {
		box-sizing: border-box;
		display: inline-block;
		width: 150px;
		height: 34px;
		line-height: 34px;
		vertical-align: top;
		font-size: 14px;
		color: #000;
	}

	> dd {
		box-sizing: border-box;
		display: inline-block;
		width: calc(100% - 150px);
		vertical-align: top;
		font-size: 14px;

		&.tel {
			line-height: 34px;
		}
	}

	.basic-txt {
		margin-right: 5px;
	}


	.desc {
		padding-top: 5px;
		font-weight: 200;
		color: #888;
	}

	@include mob {
		padding-bottom: 10px;

		> dt {
			display: block;
		}

		> dd {
			display: block;
			width: auto;
		}
		
		.flex-input {
			display: flex;

			.basic-txt {
				flex: 1;
			}

			.btn {
				width: 100px;
			}
		}

		.basic-txt.flex {
			width: 100% !important;
		}

		.desc {
			flex: 1;
			width: 100%;
		}
	}
}

/* 제목 블릿 */
.frm-basic-bu {
	padding-left: 10px;
	position: relative;

	&:after {
		content: '';
		display: block;
		width: 4px;
		height: 4px;
		margin-top: -2px;
		background: #ff0000;
		position: absolute;
		top: 50%;
		left: 0;
	}
}

/* frm 버튼 영역 */
.frm-basic-action {
	padding-top: 30px;
	text-align: center;
	border-top: 1px solid #d0d0d0;

	.btn { width: 176px; }
	.btn:not(:first-child) { margin-left: 5px; }

	@include mob {
		padding-top: 20px;
		
		.btn { width: 125px; }
	}
}

/* 통합검색 */
.global-srch-frm {
	box-sizing: border-box;
	width: 100%;
	background: #f4f4f4;
	border-bottom: 1px solid #e0e0e0;
	position: absolute;
	top: 100%;
	left: 0;
	
	.srch-field {
		width: $content-width;
		margin: 0 auto;
		padding: 30px 0;
		text-align: center;
	
		label {
			@include hidden;
		}
	}

	.srch-txt {
		box-sizing: border-box;
		width: 455px;
		height: 36px;
		line-height: 35px;
		margin-right: 10px;
		vertical-align: top;
		font-size: 16px;
		color: #000;
		background: none;
		border: 0 none;
		border-bottom: 1px solid #afafaf;

		&::-ms-clear { display: none; }
		&::-webkit-input-placeholder { color: #000; }
		&:-ms-input-placeholder { color: #000; }
		&::-moz-placeholder { color: #000; opacity: 1; }
		&:-moz-placeholder { color: #000; opacity: 1; }
	}

	.btn-submit {
		display: inline-block;
		width: 36px;
		height: 0;
		max-height: 36px;
		padding-top: 36px;
		vertical-align: top;
		background: url('../img/com/ico-top-srch.png') no-repeat 50% 50%;
		overflow: hidden;
	}

	.category {
		height: 55px;
		line-height: 55px;
		text-align: center;
		background: #fff;

		input {
			margin-right: 5px;
		}

		label {
			margin-right: 15px;
			font-weight: 200;
			font-size: 14px;
			color: #505050;
			white-space: nowrap;
		}
	}

	.category-tit {
		display: inline-block;
		margin-right: 15px;
		padding-right: 17px;
		font-size: 16px;
		color: #000;
		text-indent: 0;
		position: relative;

		&:after {
			content: '';
			display: block;
			width: 1px;
			height: 15px;
			margin-top: -6px;
			background: #d0d0d0;
			position: absolute;
			top: 50%;
			right: 0;
		}
	}
	
	@include pc-small {
		.srch-field {
			width: auto;
		}
	}

	@include mob {
		height: auto;

		.srch-txt {
			width: calc(100% - 75px);
		}

		.category {
			height: auto;
			line-height: 1.5;
			padding: 17px 15px 17px 122px;
			text-indent: -102px;
			text-align: left;
		}
	}
}

/* 리스트 상단 검색 */
.list-top-frm {
	margin-bottom: 20px;
	text-align: right;

	label { @include hidden; }

	.keyword {
		width: 200px;
	}

	.btn-submit {
		margin-left: 4px;
	}

	@include mob {
		padding-right: 74px;
		text-align: left;
		position: relative;

		.keyword {
			width: 100%;
		}

		.btn-submit {
			margin-left: 0;
			position: absolute;
			top: 0;
			right: 0;
		}
	}
}