//
// 회원가입 페이지
// --------------------------------------------------

/* 회원가입 */
.join-page {

	/* 약관동의 */
	.agree {
		@include clearfix;

		> header {
			padding-bottom: 20px;
		}

		.desc {
			font-weight: 200;
			font-size: 14px;
			color: #888;
		}

		.license {
			float: left;
			width: 48%;
		}

		.privacy {
			float: right;
		}

		h4 {
			margin-bottom: 6px;
			font-size: 16px;
			color: #000;
		}

		.txt {
			box-sizing: border-box;
			width: 100%;
			height: 180px;
			margin-bottom: 10px;
			padding: 15px;
			font-weight: 200;
			font-size: 14px;
			color: #505050;
			background: #fff;
			border: 1px solid #d0d0d0;
			overflow: scroll;
			overflow-x: hidden;
			-webkit-overflow-scrolling: touch;
		}

		label {
			margin-left: 5px;
			font-size: 14px;
			color: #505050;
		}
	}

	.license-txt {
		box-sizing: border-box;
		width: 100%;
		height: 180px;
		margin-bottom: 10px;
		padding: 15px;
		font-weight: 200;
		font-size: 14px;
		color: #505050;
		background: #fff;
		border: 1px solid #d0d0d0;
		overflow: scroll;
		overflow-x: hidden;
		-webkit-overflow-scrolling: touch;

		/* 약관 제목 depth-1 */
		.license-tit-1 {
			font-weight: bold;
			font-size: 14px;
			color: #333;

			&:not(:first-child) { margin-top: 15px; }
		}

		/* 약관 제목 depth-2 */
		.license-tit-2 {
			font-weight: bold;
			font-size: 12px;
			color: #333;

			&:not(:first-child) { margin-top: 15px; }
		}

		/* 약관 내용 문단 */
		.license-p {
			margin: 5px 0;
			font-weight: 200;
			font-size: 12px;
			color: #666;
		}
	}

	/* 정보입력 */
	.join {
		margin-top: 45px;
	}

	@include mob {
		.agree {
			> header { padding-bottom: 10px; }
			.desc { font-size: 13px; }
			.license { float: none; width: auto; padding-bottom: 10px; }
			.privacy { float: none; width: auto; }

			.txt {
				height: 120px;
			}
		}

		.join {
			margin-top: 30px;
			padding-bottom: 10px;

			> header { padding-bottom: 10px; }
		}
	}
}

/* 회원가입 완료 */
.join-ok-msg {
	box-sizing: border-box;
	max-width: 730px;
	margin: 0 auto;
	padding: 70px 0 70px 292px;
	border: 5px solid #e0e0e0;
	position: relative;

	&:after {
		content: '';
		display: block;
		width: 222px;
		height: 100%;
		background: #f4f4f4 url('../img/com/ico-logo-gray.png') no-repeat 50% 50%;
		background-size: 87px 120px;
		position: absolute;
		top: 0;
		left: 0;
	}

	.copy {
		line-height: 1.3;
		font-size: 28px;
		color: #000;
	}

	.action {
		margin-top: 25px;
	}

	.btn {
		width: 175px;

		&:not(:first-child) {
			margin-left: 5px;
		}
	}

	@include mob {
		padding: 145px 0 25px 0;
		text-align: center;

		&:after {
			width: 100%;
			height: 120px;
			background-size: 60px 83px;
		}

		.copy {
			font-size: 23px;
		}

		.action {
			margin-top: 15px;
		}

		.btn {
			display: block;
			margin: 7px auto 0;

			&:not(:first-child) {
				margin-left: auto;
			}
		}
	}
}