//
// 게시판 리스트
// --------------------------------------------------

/* 게시판 리스트 */
.board-list {
	width: 100%;
	table-layout: fixed;
	border-top: 2px solid #afafaf;
	border-bottom: 1px solid #d0d0d0;

	th {
		height: 42px;
		text-align: center;
		font-size: 14px;
		color: #000;
		background: #fafafa;
		border-bottom: 1px solid #d0d0d0;
	}

	td {
		height: 43px;
		text-align: center;
		font-weight: 200;
		font-size: 14px;
		color: #505050;
		background: #fff;
	}

	tr:not(:first-child) td {
		border-top: 1px solid #e0e0e0;
	}

	.left-cell { text-align: left; }
	.ellipsis { @include ellipsis; }

	.tit {
		font-weight: normal;
		color: #000;
	}

	td.num { font-weight: normal; font-size: 16px; color: #afafaf; }
	
	.num { width: 60px; }
	.cnt { width: 70px; }
	.date { width: 100px; }
	.title { width: auto; }

	@include mob {
		.cnt { display: none; }

		thead { display: none; }

		tr {
			display: block;
			height: 62px;
			position: relative;

			@include clearfix;
		}

		tr:not(:first-child) {
			border-top: 1px solid #e0e0e0;
		}

		td {
			float: left;
			height: auto;
			border: 0 none !important;
			background: none;
			position: relative;
		}

		.num {
			width: 50px;
			line-height: 48px;
			top: 7px;
		}

		.title {
			width: calc(100% - 50px);
			top: 12px;
		}

		.tit {
			position: relative;
		}

		.date {
			width: auto;
			padding-top: 10px;
		}
	}
}

.notice-list-no {
	width: 100%;
	padding: 120px 0;
	text-align: center;
	font-weight: 200;
	font-size: 16px;
	color: #505050;

	strong {
		font-weight: normal;
		font-weight: bold;
		font-size: 26px;
		color: #000;
	}

	&:before {
		content: '';
		display: block;
		width: 66px;
		height: 91px;
		margin: 0 auto 15px;
		background: url('../img/com/logo-gray.png') no-repeat;
		background-size: 100% 100%;
	}

	@include mob {
		padding: 50px 0;

		strong {
			font-size: 22px;
		}

		&:before {
			width: 55px;
			height: 76px;
		}
	}
}