//
// About 페이지
// --------------------------------------------------

/* About */
.about-page {
	.section-title {
		padding: 85px 0 45px;
		text-align: center;
		font-weight: bold;
		font-size: 42px;
		color: #000;

		@include tab {
			padding: 65px 0 35px;
		}

		@include mob {
			padding: 45px 0 20px;
			font-size: 26px;
		}
	}

	.btn-prev {
		width: 20px;
		height: 0;
		max-height: 46px;
		padding-top: 46px;
		background: url('../img/about/slider-prev.png') no-repeat;
		border: 0 none;
		overflow: hidden;
		position: absolute;
		top: 50%;
		left: calc(50% - 500px);
		transform: translate(0,-50%);

		@include tab {
			left: 15px;
		}

		@include mob {
			width: 14px;
			max-height: 32px;
			padding-top: 32px;
			background-size: 14px 32px;
		}
	}

	.btn-next {
		width: 20px;
		height: 0;
		max-height: 46px;
		padding-top: 46px;
		background: url('../img/about/slider-next.png') no-repeat;
		border: 0 none;
		overflow: hidden;
		position: absolute;
		top: 50%;
		right: calc(50% - 500px);
		transform: translate(0,-50%);

		@include tab {
			right: 15px;
		}

		@include mob {
			width: 14px;
			max-height: 32px;
			padding-top: 32px;
			background-size: 14px 32px;
		}
	}
}

.about-intro {
	width: 100%;
	height: 516px;
	background: url('../img/about/bg-intro.jpg') no-repeat 50% 50%;
	background-size: cover;
	position: relative;
	overflow: hidden;

	ul {
		width: 99999px;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		overflow: hidden;
	}

	li {
		width: 100%;
		height: 100%;
		position: relative;
	}

	.copy {
		text-align: center;
		line-height: 1.3;
		font-size: 36px;
		font-weight: 200;
		color: #fff;
		position: relative;
		top: 50%;
		transform: translate(0,-50%);

		span {
			line-height: 2;
			font-size: 46px;
		}

		strong {
			font-weight: normal;
		}
	}

	.paging {
		width: 100%;
		text-align: center;
		font-size: 0;
		position: absolute;
		bottom: 30px;
		left: 0;
	}

	.btn-page {
		box-sizing: border-box;
		display: inline-block;
		width: 14px;
		height: 0;
		max-height: 14px;
		padding-top: 10px;
		vertical-align: top;
		font-size: 14px;
		background: none;
		border: 2px solid #cbcaca;
		border-radius: 7px;
		overflow: hidden;

		&:not(:first-child) { margin-left: 14px; }
		&.on { background:#ff3366; border-color: #ff3366; }
	}

	@include tab {
		height: 506px;
	}

	@include mob {
		height: 246px;

		.copy {
			font-size: 24px;

			span {
				font-size: 28px;
			}
		}

		.paging {
			bottom: 20px;
		}

		.btn-page {
			width: 10px;
			max-height: 10px;
			padding-top: 6px;
			border-radius: 5px;

			&:not(:first-child) { margin-left: 10px; }
		}
	}
}

.about-platform {
	padding-bottom: 100px;

	ul {
		max-width: 1400px;
		text-align: center;
		margin: 0 auto;
		padding-top: 50px;
		font-size: 0;
		position: relative;

		&:before {
			content: '';
			display: block;
			width: 56px;
			height: 2px;
			background: #d0d0d0;
			position: absolute;
			top: 0;
			left: calc(50% - 28px);
		}
	}

	li {
		box-sizing: border-box;
		display: inline-block;
		width: 25%;
		vertical-align: top;
		font-weight: 200;
		font-size: 16px;
		color: #505050;
		white-space: nowrap;

		.tit {
			line-height: 1.3;
			font-weight: normal;
			font-size: 32px;
			color: #333;

			&:after {
				content: '';
				display: block;
				height: 200px;
			}
		}

		&:nth-child(1) .tit:after { 
			background: url('../img/about/ico-platform-1.png') no-repeat 50% 50%;
			background-size: 80px 114px;
		}

		&:nth-child(2) .tit:after { 
			background: url('../img/about/ico-platform-2.png') no-repeat 50% 50%;
			background-size: 108px 77px;
		}

		&:nth-child(3) .tit:after { 
			background: url('../img/about/ico-platform-3.png') no-repeat 50% 50%;
			background-size: 127px 105px;
		}
	}

	br.tab,
	br.mob { display: none; }

	@include tab {
		padding-bottom: 80px;

		ul {
			padding-top: 40px;
		}

		li {
			width: 30%;
		}

		br.tab { display: inline; }
	}

	@include mob {
		padding-bottom: 50px;

		ul {
			max-width: 490px;
			text-align: left;
			padding-top: 20px;

			&:before {
				content: '';
				display: block;
				width: 44px;
				height: 2px;
				background: #d0d0d0;
				position: absolute;
				top: 0;
				left: calc(50% - 22px);
			}
		}

		li {
			display: block;
			width: 100%;
			min-height: 80px;
			padding: 0 25px 0 100px;
			font-size: 14px;
			white-space: normal;
			word-wrap: break-word;
			position: relative;

			&:not(:first-child) { margin-top: 15px; }

			.tit {
				font-size: 18px;

				br { display: none; }

				&:after {
					content: '';
					display: block;
					width: 64px;
					height: 64px;
					position: absolute;
					top: 0;
					left: 15px;
				}
			}

			.tit + p {
				br { display: none; }
				br.tab { display: none; }
				br.mob { display: inline; }
			}

			&:nth-child(1) .tit:after { 
				background-size: 41px 63px;
			}

			&:nth-child(2) .tit:after { 
				background-size: 55px 40px;
			}

			&:nth-child(3) .tit:after { 
				background-size: 64px 54px;
			}
		}
	}
}

.about-flow {
	padding-bottom: 100px;
	border-top: 1px solid #e0e0e0;
	overflow: hidden;

	.flow {
		box-sizing: border-box;
		width: 1000px;
		height: 332px;
		margin: 20px auto 0 -500px;
		text-align: center;
		font-size: 16px;
		color: #000;
		position: relative;
		left: 50%;
		overflow: hidden;
	}

	.step1 {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translate(0,-50%);

		.tit {
			margin-top: 10px;
			line-height: 1.3;
		}

		.txt {
			width: 110px;
			background: url('../img/about/flow-arw-left.png') no-repeat 50% 50%;
			position: absolute;
			top: 50%;
			right: -100%;
			transform: translate(30px,-50%);
			overflow: hidden;
		}
	}

	.step2 {
		box-sizing: border-box;
		width: 332px;
		height: 332px;
		margin: 0 auto;
		font-size: 0;
		white-space: nowrap;
		border: 1px dashed #ff3b6c;
		border-radius: 50%;
		position: relative;
		z-index: 1;

		.tit {
			display: block;
			width: 100%;
			text-align: center;
			font-weight: bold;
			font-size: 18px;
			color: #ff3366;
			position: absolute;
			bottom: 47px;
			left: 0;
		}

		dd {
			display: inline-block;
			width: 132px;
			height: 132px;
			vertical-align: top;
			font-size: 16px;
			color: #fff;
			background: url('../img/about/flow-txt-pattern.png');
			border-radius: 66px;
			position: relative;
			top: 100px;
			left: -57px;

			span {
				display: block;
				position: relative;
				top: 50%;
				transform: translate(0,-50%);
			}

			&:not(:last-child) {
				margin-right: 24px;

				&:before {
					content: '';
					display: block;
					width: 0;
					height: 0;
					margin-top: -6px;
					border-top: 3px solid transparent;
					border-right: 3px solid transparent;
					border-bottom: 3px solid #ff3366;
					border-left: 3px solid #ff3366;
					position: absolute;
					top: 50%;
					left: 142px;
				}

				&:after {
					content: '';
					display: block;
					width: 0;
					height: 0;
					margin-top: -6px;
					border-top: 3px solid #ff3366;
					border-right: 3px solid transparent;
					border-bottom: 3px solid transparent;
					border-left: 3px solid #ff3366;
					position: absolute;
					top: calc(50% + 6px);
					left: 142px;
				}
			}
		}
	}

	.step3 {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(0,-50%);

		.tit {
			margin-top: 10px;
			line-height: 1.3;
		}

		.txt {
			width: 110px;
			background: url('../img/about/flow-arw-right.png') no-repeat 50% 50%;
			position: absolute;
			top: 50%;
			left: -100%;
			transform: translate(30px,-50%);
		}
	}

	.step1-img {
		width: 109px;
		height: 103px;
		margin: 0 auto;
		background: url('../img/about/flow-img-1.png') no-repeat;
		background-size: 100%;
		transform: translate(5px,0);
	}

	.step3-img {
		width: 96px;
		height: 117px;
		margin: 0 auto;
		background: url('../img/about/flow-img-2.png') no-repeat;
		background-size: 100%;
		transform: translate(20px,0);
	}

	@media screen and (min-width: #{$tablet-s-width}) {
		.step1 {
			&.before {
				.step1-img,
				.tit {
					opacity: 0;
					transform: scale(.5);
				}

				.txt {
					opacity: 0;
					transform: translate(-150px,-50%);
				}
			}

			&.after {
				.step1-img,
				.tit {
					transition: all 1s;
					opacity: 1;
					transform: scale(1);
				}

				.txt {
					transition: all .6s .6s;
					opacity: 1;
					transform: translate(30px,-50%);
				}
			}
		}

		.step2 {
			&.before {
				transform: scale(.5);
				opacity: 0;

				dd {
					opacity: 0;
					transform: scale(.5);
				}
			}

			&.after {
				transition: all .5s;
				opacity: 1;
				transform: scale(1);

				dd {
					opacity: 1;
					transform: scale(1);

					&:nth-child(2) { transition: all .6s .4s; }
					&:nth-child(3) { transition: all .6s .8s; }
					&:nth-child(4) { transition: all .6s 1.2s; }
				}
			}
		}

		.step3 {
			&.before {
				.step3-img {
					opacity: 0;
					transform: scale(.5) translate(20px,0);
				}

				.tit {
					opacity: 0;
					transform: scale(.5);
				}

				.txt {
					opacity: 0;
					transform: translate(-150px,-50%);
				}
			}

			&.after {
				.step3-img {
					opacity: 1;
					transition: all 1s .4s;
					transform: scale(1) translate(20px,0);
				}
				
				.tit {
					transition: all 1s .4s;
					opacity: 1;
					transform: scale(1);
				}

				.txt {
					transition: all .6s;
					opacity: 1;
					transform: translate(30px,-50%);
				}
			}
		}
	}

	@include tab-only {
		padding-bottom: 80px;

		.flow {
			margin-bottom: -100px;
			transform-origin: center top;
			transform: scale(.7);
		}
	}

	@include mob {
		padding-bottom: 50px;

		.flow {
			width: 100%;
			height: 550px;
			margin: 0;
			padding: 135px 0 0 0;
			font-size: 14px;
			left: 0;
		}

		.step1 {
			top: 0;
			left: 50%;
			transform: translate(-50%,0);

			.txt {
				width: auto;
				height: 74px;
				line-height: 74px;
				padding-left: 20px;
				white-space: nowrap;
				background: url('../img/about/flow-arw-top.png') no-repeat 0 50%;
				background-size: contain;
				top: 116px;
				left: 50%;
				right: auto;
				transform: translate(-3px,0);
				
				br { display: none; }
			}

			&.before {
				.step1-img,
				.tit {
					opacity: 0;
					transform: scale(.5);
				}

				.txt {
					opacity: 0;
					transform: translate(-3px,-50px);
				}
			}

			&.after {
				.step1-img,
				.tit {
					transition: all 1s;
					opacity: 1;
					transform: scale(1);
				}

				.txt {
					transition: all .6s .6s;
					opacity: 1;
					transform: translate(-3px,0);
				}
			}
		}

		.step2 {
			width: 240px;
			height: 240px;

			.tit {
				bottom: 33px;
			}

			dd {
				width: 102px;
				height: 102px;
				font-size: 14px;
				border-radius: 51px;
				position: relative;
				top: 68px;
				left: -39px;

				span {
					display: block;
					position: relative;
					top: 50%;
					transform: translate(0,-50%);
				}

				&:not(:last-child) {
					margin-right: 6px;

					&:before,
					&:after { display: none; }
				}
			}
				
			&.before {
				transform: scale(.5);
				opacity: 0;

				dd {
					opacity: 0;
					transform: scale(.5);
				}
			}

			&.after {
				transition: all .5s;
				opacity: 1;
				transform: scale(1);

				dd {
					opacity: 1;
					transform: scale(1);

					&:nth-child(2) { transition: all .6s .4s; }
					&:nth-child(3) { transition: all .6s .8s; }
					&:nth-child(4) { transition: all .6s 1.2s; }
				}
			}
		}

		.step3 {
			top: auto;
			bottom: 0;
			right: auto;
			left: 50%;
			transform: translate(-50%,0);
			
			.step3-img {
				transform: translate(15px,0);
			}

			.txt {
				width: auto;
				height: 74px;
				line-height: 74px;
				padding-left: 20px;
				white-space: nowrap;
				background: url('../img/about/flow-arw-bottom.png') no-repeat 0 50%;
				background-size: contain;
				top: auto;
				bottom: 128px;
				left: 50%;
				transform: translate(-3px,0);
				
				br { display: none; }
			}
			
			&.before {
				.step3-img {
					opacity: 0;
					transform: scale(.5) translate(15px,0);
				}

				.tit {
					opacity: 0;
					transform: scale(.5);
				}

				.txt {
					opacity: 0;
					transform: translate(-3px,-50px);
				}
			}

			&.after {
				.step3-img {
					transition: all 1s .4s;
					opacity: 1;
					transform: scale(1) translate(15px,0);
				}

				.tit {
					transition: all 1s .4s;
					opacity: 1;
					transform: scale(1);
				}

				.txt {
					transition: all .6s;
					opacity: 1;
					transform: translate(-3px,0);
				}
			}
		}

		.step1-img {
			width: 87px;
			height: 82px;
			transform: translate(5px,0);
		}

		.step3-img {
			width: 77px;
			height: 94px;
			transform: translate(20px,0);
		}
	}
}

