.about-platform2 {
	padding-bottom: 100px;

	ul {
		max-width: 1400px;
		text-align: center;
		margin: 0 auto;
		padding-top: 10px;
		font-size: 0;
		position: relative;
	}

	li {
		display: inline-block;
		width: 25%;
		vertical-align: top;
		text-align: left;
		font-weight: 200;
		font-size: 16px;
		color: #505050;
	}

	.tit {
		line-height: 1.4;
		font-weight: normal;
		font-size: 32px;
		color: #ff3366;

		&:after {
			content: '';
			display: block;
			width: 46px;
			height: 2px;
			margin: 35px 0;
			background: #d0d0d0;
		}
	}

	.con {
		color: #505050;
		p:not(:first-child) { margin-top: 10px; }
	}

	@include tab {
		padding-bottom: 80px;

		li {
			width: 30%;
			font-size: 14px;
		}
		.tit { font-size: 26px; }
	}

	@include mob {
		padding-bottom: 50px;

		li {
			width: 100%;
			text-align: center;
			font-size: 14px;

			&:not(:first-child) {
				margin-top: 30px;
			}
		}

		.tit {
			font-size: 22px;

			br:not(.mob) { display: none; }

			&:after {
				width: 30px;
				height: 2px;
				margin: 15px auto;
			}
		}
	}
}