/* 영상 등록 */
.content-add {

	.content-info {
		padding-top: 20px;
	}

	section .title {
		padding-bottom: 20px;
		font-weight: bold;
		font-size: 26px;
		color: #000;
	}

	.progress {
		margin-top: 10px;
	}

	.category-select { width: 200px; margin: 0 5px 0 0; }

	@include mob {
		.title { font-size: 22px; }
		.category-select { display: block; width: 100%; margin: 0 0 7px; }
	}
}