//
// 콘텐츠 리스트
// --------------------------------------------------

/* 콘텐츠 리스트 */
.content-list {
	width: 100%;
	table-layout: fixed;
	border-top: 2px solid #afafaf;
	border-bottom: 1px solid #d0d0d0;

	th {
		height: 42px;
		text-align: center;
		font-size: 14px;
		color: #000;
		background: #fafafa;
		border-bottom: 1px solid #d0d0d0;
	}

	td {
		height: 62px;
		text-align: center;
		font-weight: 200;
		font-size: 14px;
		color: #505050;
		background: #fff;
	}

	.wait td {
		opacity: .3;
	}

	tr:not(:first-child) td {
		border-top: 1px solid #e0e0e0;
	}

	.left-cell { text-align: left; }
	.ellipsis { @include ellipsis; }

	.tit {
		font-weight: normal;
		color: #000;
	}

	.thumb {
		display: inline-block;
		width: 48px;
		height: 48px;
		margin-right: 14px;
		vertical-align: middle;
		background: #000;
		position: relative;
		overflow: hidden;

		img {
			height: 100%;
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%,0);
		}

		a:after {
			content: '';
			display: none;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,.55) url('../img/com/ico-thumb-play.svg') no-repeat 50% 50%;
			background-size: 20px 20px;
			position: absolute;	
			top: 0;
			left: 0;
		}

		a:hover:after { display: block; }
	}

	.rank-flag {
		display: inline-block;
		width: 34px;
		height: 51px;
		text-align: center;
		line-height: 48px;
		font-size: 16px;
		color: #fff;
		background-size: cover;

		&.rank-1 { background-image: url('../img/com/bg-rank-1.png'); }
		&.rank-2 { background-image: url('../img/com/bg-rank-2.png'); }
		&.rank-3 { background-image: url('../img/com/bg-rank-3.png'); }
	}

	td.num { font-weight: normal; font-size: 16px; color: #afafaf; }
	
	.num { width: 60px; }
	.title { width: auto; }
	.name { width: 80px; }
	.original { width: 80px; }
	.play { width: 60px; }
	.like { width: 60px; }
	.view { width: 60px; }
	.put { width: 50px; }
	.edit { width: 50px; }
	.del { width: 50px; }
	.share { width: 50px; }

	@include tab {
		.original,
		.like,
		.view { display: none; }
	}

	@include mob {
		.play,
		.put,
		.share { display: none; }

		thead { display: none; }

		tr {
			display: block;
			height: 62px;
			position: relative;

			@include clearfix;
		}

		tr:not(:first-child) {
			border-top: 1px solid #e0e0e0;
		}

		td {
			float: left;
			height: auto;
			border: 0 none !important;
			position: relative;
		}

		.num {
			width: 50px;
			line-height: 48px;
			top: 7px;
		}

		.thumb {
			margin-right: 5px;
		}

		.title {
			width: calc(100% - 50px);
			top: 7px;
		}

		.tit {
			position: relative;
			top: -8px;
		}

		.name {
			width: calc(100% - 106px);
			text-align: left;
			background: none;
			position: absolute;
			top: 30px;
			left: 106px;
		}

		&.my-list {
			.name { left: 56px; }
			.del { top: 18px; }
		}

		&.srch-list {
			.name { left: 56px; }
			.put { display: block; top: 18px; }
		}

		&.profile-list {
			.name {left: 56px; }
			.put { display: block; top: 18px; }
		}

		&.my-profile-list {
			.title { width: calc(100% - 100px); }
			.name {left: 56px; }
			.edit { top: 18px; }
			.del { top: 18px; }
		}
	}
}

.content-list-no {
	width: 100%;
	padding: 120px 0;
	text-align: center;
	font-weight: 200;
	font-size: 16px;
	color: #505050;

	strong {
		font-weight: normal;
		font-weight: bold;
		font-size: 26px;
		color: #000;
	}

	&:before {
		content: '';
		display: block;
		width: 66px;
		height: 91px;
		margin: 0 auto 15px;
		background: url('../img/com/logo-gray.png') no-repeat;
		background-size: 100% 100%;
	}

	@include mob {
		padding: 50px 0;

		strong {
			font-size: 22px;
		}

		&:before {
			width: 55px;
			height: 76px;
		}
	}
}