//
// 로그인 페이지
// --------------------------------------------------

/* 로그인 */
.login-frm {
	box-sizing: border-box;
	max-width: 730px;
	margin: 0 auto;
	padding: 50px 70px;
	border: 5px solid #e0e0e0;
	position: relative;

	.copy {
		padding-bottom: 30px;
		text-align: center;

		br { display: none; }

		.main {
			font-size: 28px;
			color: #000;
		}

		.sub {
			font-weight: 200;
			font-size: 16px;
			color: #505050;
		}
	}

	.wrap {
		position: relative;
		@include clearfix;

		&:before {
			content: '';
			display: block;
			width: 1px;
			height: 100%;
			background: #d0d0d0;
			position: absolute;
			top: 0;
			left: 64.5%;
		}
	}

	.login {
		float: left;
		width: 59%;
	}

	.social {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 30%;
		height: 100%;
		position: absolute;
		top: 0;
		right: 0;

		.social-login {
			height: 40px;
			line-height: 40px;
			color: #fff;

			&.facebook { background: #3b5998; }
			&.google { background: #db4437; }
			&.naver { background: #2db400; }
		}
	}

	.frm-area {
		padding-right: 89px;
		position: relative;
		
		label {
			display: block;
			position: relative;
			overflow: hidden;

			span {
				padding-left: 10px;
				line-height: 34px;
				font-weight: 200;
				font-size: 14px;
				color: #888;
				position: absolute;
				top: 0;
				left: 0;
			}

			&.passwd {
				margin-top: 7px;
			}
		}

		input {
			display: block;
			width: 100%;
			background: transparent;
			position: relative;
			z-index: 1;

			&.has-value + span {
				left: -9999px;
			}
		}

		.btn-login {
			width: 82px;
			height: 100%;
			position: absolute;
			top: 0;
			right: 0;
		}
	}

	.action {
		padding-top: 13px;
		line-height: 1.6;
		font-weight: 200;
		font-size: 14px;
		color: #888;

		a {
			color: #505050;

			&.join-link {
				font-weight: normal;
				text-decoration: underline;
			}
		}
	}

	@include mob {
		padding: 8%;
	}

	@include mob-large {
		.copy {
			padding-bottom: 8%;

			br { display: inline; }
			.main { font-size: 23px; }
			.sub { font-size: 16px; }
		}

		.wrap:before { display: none; }

		.login {
			float: none;
			width: 100%;
			padding-bottom: 8%;
			margin-bottom: 8%;
			
			border-bottom: 1px solid #d0d0d0;
		}

		.social {
			position: static;
			width: 63%;
			height: auto;
			margin: 0 auto;

			.social-login:not(:first-child) {
				margin-top: 7px;
			}
		}

		.action {
			text-align: center;
		}
	}

	@include mob-small {
		.social { width: 80%; }
	}

	@include mob-small {
		.copy {
			.main { font-size: 20px; }
			.sub { font-size: 14px; }
		}
	}
}
