//
// 레이아웃 스타일
// --------------------------------------------------

/* 스킵 내비게이션 */
.skip-navi {
	@include hidden;

	&:focus {
		display: block;
		width: 100%;
		height: 40px;
		line-height: 40px;
		text-align: center;
		font-size: 16px;
		color: #fff;
		text-decoration: none;
		background: #0a2e6b;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 99999;
	}
}

/* 레이아웃 컨테이너 */
.wrapper {
	box-sizing: border-box;
	width: 100%;
	min-height: 100%;
	padding: 0 0 85px 0;
	position: absolute;
	top: 0;
	left: 0;
}

/* 공통 페이지 */
.common-page {
	box-sizing: border-box;
	padding: 0 0 70px;
}

/* 공통 페이지 내용 */
.common-content {
	box-sizing: border-box;
	width: $content-width;
	margin: 60px auto 0;
}

@include pc-large {
	.wrapper { padding-left: 190px; }
}

@include pc-small {
	.wrapper { padding-top: 70px; }
	.common-page { padding: 0 0 8%; }
	.common-content { width: $content-width; margin: 50px auto 0; }
}

@include tab {
	.common-page { padding: 0 0 8%; }
	.common-content { width: auto; padding: 0 4%; margin: 40px auto 0; }
}