//
// 진행바 스타일
// --------------------------------------------------

/* 진행바 */
.progress {
	font-size: 0;
	white-space: nowrap;

	@include clearfix;

	.percent {
		display: inline-block;
		width: 50px;
		vertical-align: middle;
		text-align: right;
		font-weight: 200;
		font-size: 14px;
		color: #888;
	}

	.bar {
		display: inline-block;
		width: calc(100% - 50px);
		height: 6px;
		vertical-align: middle;
		background: #f4f4f4;

		div {
			width: 0;
			height: 100%;
			background: #ff5e46;
		}
	}
}