// --------------------------------------------------

/* 리스트 날짜 선택 */
.list-date {
	padding: 28px 0;
	text-align: center;

	.wrap {
		display: inline-block;
		padding: 0 40px;
		position: relative;
	}

	.date {
		font-weight: bold;
		font-size: 26px;
		color: #000;
	}

	.time {
		color: #888;
	}

	.btn-arw {
		display: block;
		width: 18px;
		height: 0;
		max-height: 22px;
		padding-top: 22px;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		background-size: 8px 12px;
		position: absolute;
		top: 50%;
		transform: translate(0,-50%);
		overflow: hidden;
	}

	.prev {
		background-image: url('../img/com/date-prev.png');
		right: 100%;
	}

	.next {
		background-image: url('../img/com/date-next.png');
		left: 100%;
	}

	@include mob {
		padding: 4% 0;

		.wrap {
			padding: 0 4%;
		}
		.date {
			font-size: 24px;
		}
	}
}