//
// 댓글 스타일
// --------------------------------------------------

/* 댓글 */
.comment {
	.comment-header {
		padding-bottom: 10px;

		.section-title {
			font-weight: bold;
			font-size: 18px;
			color: #000;

			.cnt {
				margin-left: 5px;
				font-weight: normal;
				font-size: 14px;
				color: #707070;
			}

			.num {
				font-weight: bold;
				color: $main-color;
			}
		}
	}
}

.comment-frm {
	margin-bottom: 15px;
	padding: 25px 120px 25px 110px;
	background: #f4f4f4;
	position: relative;

	label {
		@include hidden;
	}

	.ico-user-big {
		position: absolute;
		top: 25px;
		left: 25px;
	}

	.txt-input {
		box-sizing: border-box;
		width: 100%;
		height: 70px;
		padding: 10px;
		text-align: left;
		background: #fff;
		border: 1px solid #d0d0d0;
		vertical-align: top;
	}

	.btn-submit {
		box-sizing: border-box;
		display: inline-block;
		width: 82px;
		height: 70px;
		line-height: 70px;
		text-align: center;
		font-size: 14px;
		color: #fff;
		background: #000;
		border: 0 none;
		position: absolute;
		top: 25px;
		right: 25px;
	}
}

.comment-list {
	font-size: 14px;
	color: #505050;

	.info {
		color: #000;
	}

	.info-txt {
		margin-left: 12px;
		color: #888;
	}
}

.comment-no-list {
	width: 100%;
	padding: 40px 0;
	text-align: center;
	font-weight: 200;
	font-size: 16px;
	color: #505050;

	&:before {
		content: '';
		display: block;
		width: 66px;
		height: 91px;
		margin: 0 auto 15px;
		background: url('../img/com/logo-gray.png') no-repeat;
		background-size: 100% 100%;
	}
}

.comment-item {
	padding: 15px 0;
	border-bottom: 1px solid #e0e0e0;

	.comment-txt {
		padding-top: 5px;
		padding-left: 32px;
		font-weight: 200;
		word-wrap: break-word;
	}
}

@include mob {
	.comment-frm {
		margin-bottom: 5px;
		text-align: right;
		padding: 22px 15px 22px 54px;

		.ico-user-big {
			top: 22px;
			left: 15px;
		}

		.txt-input {
			box-sizing: border-box;
			width: 100%;
			height: 33px;
			padding: 5px;
			background: #fff;
			border: 1px solid #d0d0d0;
			border-radius: 0;
		}

		.btn-submit {
			display: none;
			height: 34px;
			line-height: 34px;
			margin-top: 7px;
			position: static;
		}

		&.is-focus {
			.btn-submit {
				display: inline-block;
			}
		}
	}

	.comment-no-list {
		padding: 20px 0;

		&:before {
			width: 55px;
			height: 76px;
		}
	}
}