//
// 탭메뉴
// --------------------------------------------------

/* 블록 스타일 탭메뉴 */
.block-tab-menu {
	font-size: 0;
	text-align: center;

	li {
		display: inline-block;
		width: 156px;
	}

	a {
		box-sizing: border-box;
		display: block;
		height: 40px;
		line-height: 38px;
		text-align: center;
		font-size: 16px;
		color: #000;
		text-decoration: none;
		background: #fff;
		border: 1px solid #d0d0d0;

		&:hover,
		&:focus {
			background: #fafafa;
		}
	}

	li:not(:first-child) a {
		margin-left: -1px;
	}

	li.on a {
		color: #fff;
		background-color: #000;
		border-color: #000;

		&:hover,
		&:focus {
			background: rgba(0,0,0,.8);
		}
	}

	@include tab {
		li { width: 20%; }
	}

	@include mob {
		text-align: left;
		li { width: 33.33%; margin-bottom: 5px; }
		a { font-size: 14px; }
	}
}