//
// 유틸리티 스타일
// --------------------------------------------------

/* 공통 유틸리티 영역 */
.common-util {
	box-sizing: border-box;
	height: 84px;
	text-align: right;
	position: relative;
	z-index: 1;

	.wrap {
		width: $content-width;
		margin: 0 auto;
		padding-top: 30px;
	}

	.nav {
		display: inline-block;
		vertical-align: middle;
		font-weight: 200;
		font-size: 13px;
		color: #888;

		@include clearfix;

		li {
			float: left;
			position: relative;

			&:not(:first-child) {
				margin-left: 10px;
				padding-left: 12px;

				&:after {
					content: '/';
					display: block;
					position: absolute;
					top: 0;
					left: -2px;
				}
			}
		}

		a:hover,
		a:focus {
			color: #222;
		}
	}

	.srch-toggle {
		display: inline-block;
		box-sizing: border-box;
		width: 26px;
		height: 26px;
		margin-left: 20px;
		vertical-align: middle;
		color: transparent;
		background: url('../img/com/ico-srch-open.png') no-repeat;
		border: 0 none;
		transition: all .4s;
		position: relative;
		top: -3px;
		overflow: hidden;

		.util-srch-open & { background: url('../img/com/ico-srch-close.png') no-repeat; }
	}

	.srch-frm {
		display: none;

		.util-srch-open & { display: block; }
	}

	
	@include pc-small {
		.wrap {
			width: auto;
			margin: 0 4%;
		}
	}

	@include tab {
	}

	@include mob {
		display: none;
	}
}