// --------------------------------------------------

/* 게시판 상세 */
.board-view {
	.common-title {
		border-bottom: 2px solid #afafaf;

		.title {
			font-weight: normal;
			font-size: 18px;
		}

		.info {
			float: right;
			padding-top: 20px;
			font-weight: 200;
			font-size: 14px;
			color: #707070;
		}
	}

	.content-area {
		padding: 30px 0;
		font-weight: 200;
		font-size: 14px;
		color: #505050;
		border-bottom: 1px solid #d0d0d0;
	}

	.foot {
		margin-top: 30px;
		text-align: center;

		.btn {
			width: 175px;
		}
	}

	@include mob {
		.common-title {
			.info {
				float: left;
				padding-top: 10px;
			}
		}

		.content-area {
			padding: 4% 0;
		}

		.foot {
			margin-top: 4%;
		}
	}
}