//
// 아이콘 스타일
// --------------------------------------------------

/* 유저 아이콘 - small */
.ico-user {
	display: inline-block;
	width: 24px;
	height: 24px;
	background: url('../img/com/ico-people.png') no-repeat;
	background-size: 24px 24px;
	border-radius: 12px;
	overflow: hidden;

	/* 텍스트와 함께 inline으로 쓰일때 위치조정 */
	&.inline {
		margin-top: -2px;
		margin-right: 5px;
		vertical-align: middle;
	}
}

/* 유저 아이콘 - medium */
.ico-user-medium {
	display: inline-block;
	width: 34px;
	height: 34px;
	background: url('../img/com/ico-people.png') no-repeat;
	background-size: 34px 34px;
	border-radius: 17px;
	overflow: hidden;

	/* 텍스트와 함께 inline으로 쓰일때 위치조정 */
	&.inline {
		margin-top: -2px;
		margin-right: 5px;
		vertical-align: middle;
	}
}


/* 유저 아이콘 - big */
.ico-user-big {
	display: inline-block;
	width: 70px;
	height: 70px;
	vertical-align: middle;
	background: url('../img/com/ico-people.png') no-repeat;
	background-size: 70px 70px;
	border-radius: 35px;
	overflow: hidden;

	/* 텍스트와 함께 inline으로 쓰일때 위치조정 */
	&.inline {
		margin-right: 5px;
	}

	@include mob {
		width: 34px;
		height: 34px;
		background-size: 34px 34px;
		border-radius: 17px;
	}
}
.ico-user-big2 {
	@extend .ico-user-big;

	width: 130px;
	height: 130px;
	background-size: 130px 130px;
	border-radius: 65px;
}

/* 인라인 아이콘 스타일 */
.ico-write {
	display: inline-block;
	width: 13px;
	height: 13px;
	margin-top: -2px;
	vertical-align: middle;
	background: url('../img/com/ico-write.png');
}
.ico-del {
	display: inline-block;
	width: 12px;
	height: 12px;
	margin-top: -2px;
	vertical-align: middle;
	background: url('../img/com/ico-del.png');
}
.ico-public {
	display: inline-block;
	width: 14px;
	height: 14px;
	margin-top: -2px;
	vertical-align: middle;
	background: url('../img/com/ico-public.png');
}
.ico-private {
	display: inline-block;
	width: 11px;
	height: 14px;
	margin-top: -2px;
	vertical-align: middle;
