//
// 이용약관 스타일
// --------------------------------------------------

/* 이용약관 페이지 */
.license-page {
	/* 약관 제목 depth-1 */
	.license-tit-1 {
		font-weight: bold;
		font-size: 16px;
		color: #333;

		&:not(:first-child) { margin-top: 20px; }
	}

	/* 약관 제목 depth-2 */
	.license-tit-2 {
		font-weight: bold;
		font-size: 13px;
		color: #333;

		&:not(:first-child) { margin-top: 20px; }
	}

	/* 약관 내용 문단 */
	.license-p {
		margin: 5px 0;
		font-size: 13px;
		color: #999;
	}
}