//
// 메인 스타일
// --------------------------------------------------

/* 메인 페이지 */
.site-index {
	h2 {
		@include hidden;
	}

	// 메인 콘텐츠 영역
	.main-con {
		max-width: $content-width;
		margin: 60px auto 0;
		padding-bottom: 72px;

		@include clearfix;

		@include tab {
			margin: 50px 4% 0;
			padding-bottom: 8%;
		}

		@include mob {
			margin: 8% 4% 0;
		}
	}

	// 더보기 버튼
	.link-more {
		display: inline-block;
		padding-right: 20px;
		font-size: 14px;
		color: #000;
		background: url('../img/com/ico-link-more.png') no-repeat 100% 50%;
		position: absolute;
		top: 37px;
		right: 0;

		@include mob {
			top: 20px;
		}
	}
}

.index-chart {
	float: left;
	width: 58%;
	position: relative;

	.tab {
		border-top: 2px solid #afafaf;
		border-bottom: 1px solid #d0d0d0;

		@include clearfix;

		a {
			box-sizing: border-box;
			float: left;
			width: 20%;
			height: 44px;
			line-height: 44px;
			text-align: center;
			font-size: 16px;
			color: #333;
			text-decoration: none;
			position: relative;

			&:not(:first-child):after {
				content: '';
				display: block;
				width: 1px;
				height: 12px;
				margin-top: -6px;
				background: #d0d0d0;
				position: absolute;
				top: 50%;
				left: 0;
			}

			&.on {
				font-weight: bold;
				color: #ff0000;
			}
		}
	}

	.list {
		border-bottom: 1px solid #d0d0d0;

		> li {
			height: 62px;
			padding: 0 115px 0 115px;
			line-height: 62px;
			position: relative;

			&:not(:first-child) {
				border-top: 1px solid #e0e0e0;
			}

			&:hover .tit,
			&:hover .num { color: #000; }
		}
	}

	.thumb {
		display: block;
		width: 48px;
		height: 48px;
		background: #000;
		position: absolute;
		top: 7px;
		left: 50px;
		overflow: hidden;

		img {
			height: 100%;
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%,0);
		}

		a:after {
			content: '';
			display: none;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,.55) url('../img/com/ico-thumb-play.svg') no-repeat 50% 50%;
			background-size: 20px 20px;
			position: absolute;	
			top: 0;
			left: 0;
		}

		a:hover:after { display: block; }
	}

	.tit {
		font-size: 14px;
		color: #000;

		@include ellipsis;
	}

	.info {
		width: 115px;
		text-align: center;
		font-weight: 200;
		font-size: 14px;
		color: #707070;
		position: absolute;
		top: 0;
		right: 0;
	}

	.num {
		width: 50px;
		height: 62px;
		text-align: center;
		font-size: 16px;
		color: #afafaf;
		position: absolute;
		top: 0;
		left: 0;
	}

	@include mob {
		float: none;
		width: auto;
		
		.list {
			> li {
				height: 62px;
				padding: 0 65px 0 110px;
				line-height: 62px;
			}
		}

		.thumb {
			left: 45px;
		}

		.info {
			width: 65px;
		}

		.num {
			width: 45px;
		}
	}
}

.index-hot {
	float: right;
	width: 36%;
	position: relative;

	.list {
		border-top: 2px solid #afafaf;
		border-bottom: 1px solid #d0d0d0;

		> li {
			height: 134px;
			padding: 0 0 0 123px;
			position: relative;

			&:not(:first-child) {
				border-top: 1px solid #e0e0e0;
			}
		}

		.thumb {
			display: block;
			width: 106px;
			height: 106px;
			background: #000;
			position: absolute;
			top: 14px;
			left: 0;
			overflow: hidden;

			img {
				height: 100%;
				position: absolute;
				top: 0;
				left: 50%;
				transform: translate(-50%,0);
			}

			a:after {
				content: '';
				display: none;
				width: 100%;
				height: 100%;
				background: rgba(0,0,0,.55) url('../img/com/ico-thumb-play.svg') no-repeat 50% 50%;
				background-size: 35px 35px;
				position: absolute;	
				top: 0;
				left: 0;
			}

			a:hover:after { display: block; }
		}

		.con {
			position: relative;
			top: 50%;
			transform: translate(0, -50%);
		}

		.tit {
			max-height: 42px;
			line-height: 21px;
			font-size: 16px;
			color: #000;
			word-wrap: break-word;
			overflow: hidden;
		}

		.info {
			margin-top: 3px;
			line-height: 21px;
			font-weight: 200;
			font-size: 14px;
			color: #707070;
		}
	}

	@include mob {
		float: none;
		width: auto;
		margin-top: 8%;
	}
}

/* 상단 검색 */
.index-top-srch {
	box-sizing: border-box;
	padding: 133px 4%;
	background: url('../img/main/index-top-bg.jpg') no-repeat 50% 50%;
	background-size: cover;

	.copy {
		margin-bottom: 39px;
		line-height: 1.33;
		text-align: center;
		font-size: 36px;
		color: #fff;
		letter-spacing: -0.05em;

		span { white-space: nowrap; }
		.break-1 { display: none; }
	}

	@include pc-only {
		height: 496px;
	}

	@include tab {
		padding: 81px 4%;
		height: 390px;
	}

	@include mob {
		padding: 55px 4% 30px;
		height: auto;

		.copy {
			margin-bottom: 30px;
			font-size: 28px;

			.break-1 { display: inline; }
		}
	}
}

.index-srch-frm {
	width: 620px;
	margin: 0 auto;

	.srch-field {
		box-sizing: border-box;
		height: 54px;
		padding: 0 75px 0 25px;
		border: 2px solid #fff;
		border-radius: 26px;
		position: relative;

		label {
			@include hidden;
		}
	}

	.srch-txt {
		box-sizing: border-box;
		width: 100%;
		height: 50px;
		line-height: 50px;
		margin-right: 10px;
		vertical-align: top;
		font-weight: 200;
		font-size: 18px;
		color: #fff;
		background: none;
		border: 0 none;

		&::-ms-clear { display: none; }
		&::-webkit-input-placeholder { color: #fff; }
		&:-ms-input-placeholder { color: #fff; }
		&::-moz-placeholder { color: #fff; opacity: 1; }
		&:-moz-placeholder { color: #fff; opacity: 1; }
	}

	.btn-submit {
		display: inline-block;
		width: 75px;
		height: 0;
		max-height: 50px;
		padding-top: 50px;
		vertical-align: top;
		background: url('../img/main/index-srch-btn.png') no-repeat 50% 50%;
		background-size: 30px 30px;
		position: absolute;
		top: 0;
		right: 0;
		overflow: hidden;
	}

	.category {
		margin-top: 8px;
		line-height: 30px;
		text-align: center;

		input {
			margin-right: 5px;
			position: relative;
			top: -2px;
		}

		label {
			margin-right: 15px;
			font-weight: 200;
			font-size: 14px;
			color: #fff;
			white-space: nowrap;

			&:last-child { margin-right: 0; }
		}
	}

	.category-tit {
		display: inline-block;
		margin-right: 15px;
		padding-right: 17px;
		font-size: 16px;
		color: #fff;
		text-indent: 0;
		position: relative;

		&:after {
			content: '';
			display: block;
			width: 1px;
			height: 15px;
			margin-top: -6px;
			background: #888;
			position: absolute;
			top: 50%;
			right: 0;
		}
	}

	@include mob {
		width: auto;

		.copy {
			margin-bottom: 30px;
			font-size: 28px;

			.break-1 { display: inline; }
		}

		.srch-field {
			height: 44px;
			padding: 0 65px 0 20px;
			border-radius: 22px;
		}

		.srch-txt {
			height: 40px;
			line-height: 40px;
			font-size: 16px;
		}

		.btn-submit {
			width: 65px;
			max-height: 40px;
			padding-top: 40px;
			background-size: 25px 25px;
		}

		.category {
			line-height: 1.5;
			padding: 17px 15px 17px 122px;
			text-indent: -102px;
			text-align: left;
		}
	}
}