//
// 타이틀
// --------------------------------------------------

/* 페이지 타이틀 */
.common-page-title {
	box-sizing: border-box;
	height: 215px;
	padding: 46px 4% 4%;
	text-align: center;
	background: url('../img/com/bg-page-title.jpg') no-repeat 50% 50%;
	background-size: cover;

	.title {
		margin-bottom: 13px;
		padding-bottom: 9px;
		font-weight: bold;
		font-size: 46px;
		color: #fff;
		position: relative;

		&:after {
			content: '';
			display: block;
			width: 46px;
			height: 3px;
			margin-left: -23px;
			background: #f7ee13;
			position: absolute;
			bottom: 0;
			left: 50%;
		}
	}

	.desc {
		font-weight: 200;
		font-size: 16px;
		color: #fff;
	}
}

/* 본문 타이틀 */
.common-title {
	padding-top: 10px;
	position: relative;

	@include clearfix;

	&:before {
		content: '';
		display: block;
		width: 28px;
		height: 10px;
		transform: skew(30deg);
		background: red;
		position: absolute;
		top: 0;
		left: -17px;
	}

	.title {
		display: inline-block;
		padding: 18px 0 20px 0;
		vertical-align: baseline;
		line-height: 1.2;
		font-weight: bold;
		font-size: 26px;
		color: #000;
	}

	.desc {
		display: inline-block;
		margin-left: 8px;
		vertical-align: baseline;
		font-weight: 200;
		font-size: 16px;
		font-weight: 200;
		color: #505050;

		em {
			color: $main-color;
		}
	}
}

@include pc-small {
	.common-page-title {
		width: 100%;
		position: static;
	}
}

@include tab {
	.common-title:before {
		left: -8px;
	}
}

@include mob {
	.common-page-title {
		height: auto;
		min-height: 196px;

		.title {
			font-size: 32px;
		}
	}

	.common-title {
		padding-top: 8px;
		padding-bottom: 8px;

		&:before {
			width: 20px;
			height: 8px;
			left: -6px;
		}

		.title {
			padding: 10px 0 0;
			font-size: 20px;
		}

		.desc {
			display: block;
			margin-top: 5px;
			margin-left: 0;
			font-size: 14px;
			color: #505050;
		}
	}
}