//
// 인트로 페이지
// --------------------------------------------------

/* 인트로 */
.intro-page {
	width: 100%;
	height: 100%;
	min-height: 700px;
	background: #000 url('../img/intro/intro-bg-default.jpg') no-repeat 50% 50%;
	background-size: cover;
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;

	/* 오버시 나오는 bg */
	.bg {
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		background-size: cover !important;
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
		transition: all .6s;

		&.bg1 { background: url('../img/intro/intro-bg-1.jpg') no-repeat 50% 50%; }
		&.bg2 { background: url('../img/intro/intro-bg-2.jpg') no-repeat 50% 50%; }
		&.bg3 { background: url('../img/intro/intro-bg-3.jpg') no-repeat 50% 50%; }
		&.bg4 { background: url('../img/intro/intro-bg-4.jpg') no-repeat 50% 50%; }
	}

	/* 로고 */
	&:after {
		content: '';
		display: block;
		width: 76px;
		height: 104px;
		background: url('../img/com/logo.svg') no-repeat;
		background-size: 100%;
		position: absolute;
		top: 45px;
		left: 45px;
	}

	/* 스킵 링크 */
	.skip {
		display: block;
		padding-right: 35px;
		font-size: 22px;
		color: #707070;
		text-decoration: none;
		background: url('../img/intro/skip-bg.png') no-repeat 100% 15px;
		background-size: 28px 11px;
		position: absolute;
		bottom: 45px;
		right: 45px;

		&:hover {
			color: #fff;
		}
	}

	.nav {
		width: 100%;
		margin-top: -60px;
		text-align: center;
		font-size: 0;
		white-space: nowrap;
		position: absolute;
		top: 50%;
		left: 0;

		.item {
			display: inline-block;
			vertical-align: top;

			&:not(:first-child) {
				margin-left: 30px;
			}
		}

		br { display: none; }

		a {
			display: block;
			height: 100%;
		}

		@include pc-only {
			.tit {
				display: block;
				width: 100%;
				text-align: center;
				font-weight: 200;
				font-size: 60px;
				color: #fff;
				opacity: 0;
				letter-spacing: -3px;
				position: absolute;
				top: -250px;
				left: 0;
				transition: all .4s;
			}

			.on .tit { opacity: 1; }
		}
	}

	/* 아이콘 영역 */
	.ico {
		display: block;
		width: 196px;
		height: 196px;
		position: relative;

		span {
			display: block;
			width: 100%;
			height: 100%;
			position: relative;

			/* 아이콘 */
			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 100%;
				position: relative;
				z-index: 1;
			}
		}
	}

	.seq1 {
		.ico span:after { background: url('../img/intro/intro-ico-1.svg') no-repeat 50% 50%; }
		&.on .ico span:after { background: url('../img/intro/intro-ico-1-on.svg') no-repeat 50% 50%; }

		.ico:before {
			content: '';
			display: block;
			width: 1000px;
			height: 1px;
			background: rgba(255,255,255,.2);
			position: absolute;
			top: 50%;
			right: 160px;
		}
	}

	.seq2 {
		margin-top: -96px;
		&.on { margin-top: -106px; }
		.ico span:after { background: url('../img/intro/intro-ico-2.svg') no-repeat 50% 50%; }
		&.on .ico span:after { background: url('../img/intro/intro-ico-2-on.svg') no-repeat 50% 50%; }

		.ico:before {
			content: '';
			display: block;
			width: 100px;
			height: 1px;
			background: rgba(255,255,255,.2);
			position: absolute;
			top: 86%;
			left: -65px;
			transform-origin: bottom left;
			transform: rotate(-20deg);
		}
	}

	.seq3 {
		margin-top: 30px;
		&.on { margin-top: 50px; }
		.ico span:after { background: url('../img/intro/intro-ico-3.svg') no-repeat 50% 50%; }
		&.on .ico span:after { background: url('../img/intro/intro-ico-3-on.svg') no-repeat 50% 50%; }

		.ico:before {
			content: '';
			display: block;
			width: 100px;
			height: 1px;
			background: rgba(255,255,255,.2);
			position: absolute;
			top: 32%;
			left: -70px;
			transform-origin: bottom right;
			transform: rotate(30deg);
		}
	}

	.seq4 {
		margin-top: -56px;
		.ico span:after { background: url('../img/intro/intro-ico-4.svg') no-repeat 50% 50%; }
		&.on .ico span:after { background: url('../img/intro/intro-ico-4-on.svg') no-repeat 50% 50%; }

		.ico:before {
			content: '';
			display: block;
			width: 100px;
			height: 1px;
			background: rgba(255,255,255,.2);
			position: absolute;
			top: 80%;
			left: -65px;
			transform-origin: bottom left;
			transform: rotate(-20deg);
		}

		.ico:after {
			content: '';
			display: block;
			width: 1000px;
			height: 1px;
			background: rgba(255,255,255,.2);
			position: absolute;
			top: 50%;
			left: 170px;
		}
	}

	// 오버 상태별 스타일
	&[data-seq='1'] {
		.bg1 { opacity: 1; }
	}
	&[data-seq='2'] {
		.bg2 { opacity: 1; }
		.seq2 .ico:before {
			transform: rotate(-30deg);
		}
		.seq3 .ico:before {
			transform: rotate(40deg);
		}
	}
	&[data-seq='3'] {
		.bg3 { opacity: 1; }
		.seq3 .ico:before {
			top: 31%;
			left: -80px;
			transform: rotate(40deg);
		}
		.seq4 .ico:before {
			top: 90%;
			left: -50px;
			transform: rotate(-35deg);
		}
	}
	&[data-seq='4'] {
		.bg4 { opacity: 1; }
		.seq4 .ico:before {
			transform: rotate(-30deg);
		}
	}

	@include tab {
		&:after {
			width: 66px;
			height: 91px;
		}

		.nav {
			margin-top: -188px;

			br { display: inline; }

			.item:not(:first-child) {
				margin-left: 0;
			}

			.tit {
				display: block;
				text-align: center;
				line-height: 1.3;
				font-weight: 200;
				font-size: 18px;
				color: #fff;
				position: relative;
				top: -42px;
			}
		}

		.seq1 {
			.tit { left: -5px; }
			.ico:before {
				top: 55%;
			}
		}

		.seq2 {
			margin-top: -55px;
			.tit { left: -5px; }
			.ico:before {
				width: 85px;
				top: 80%;
				left: -40px;
			}
		}

		.seq3 {
			margin-top: 0;
			.ico:before {
				display: none;
			}
		}

		.seq4 {
			margin-top: -55px;
			.ico:before {
				width: 85px;
				top: 76%;
				left: -35px;
			}
		}
	}

	@include mob {
		min-height: 480px;

		&:after {
			width: 46px;
			height: 63px;
			top: 20px;
			left: 20px;
		}

		.skip {
			padding-right: 28px;
			font-size: 16px;
			background-position: 100% 10px;
			background-size: 23px 9px;
			position: absolute;
			bottom: 20px;
			right: 20px;
		}

		.nav {
			margin-top: -130px;

			br { display: inline; }

			.tit {
				font-size: 13px;
				top: -31px;
			}
		}
		
		.seq1 .tit {
			left: 0;
		}
		
		.seq2 .tit {
			left: -2px;
		}

		.ico {
			width: 130px;
			height: 130px;

			span:after {
				transform: scale(.66);
			}
		}

		.seq1 .ico:before {
			top: 55%;
			right: 110px;
		}
		
		.seq2 .ico:before {
			width: 50px;
			left: -20px;
			top: 87%;
			transform: rotate(-30deg);
		}
		
		.seq3 .ico:before {
			display: none;
		}

		.seq4 .ico:before {
			width: 50px;
			left: -20px;
			top: 85%;
			transform: rotate(-30deg);
		}
		
		.seq4 .ico:after {
			left: 110px;
		}
	}
}

/* 인트로 상세 팝업 */
.intro-detail-view {
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	padding: 8%;
	text-align: center;
	white-space: nowrap;
	position: fixed;
	top: 0;
	left: 0;
	overflow: auto;
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
	z-index: 1000;

	// 상대적으로 센터 정렬 위한 요소
	&:after {
		content: '';
		display: inline-block;
		width: 0;
		height: 100%;
		vertical-align: middle;
	}

	&:before {
		content: '';
		box-sizing: border-box;
		display: block;
		width: 100%;
		min-width: $site-min-width;
		height: 100%;
		background: rgba(0,0,0,.8);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
	}

	.close {
		box-sizing: border-box;
		width: 79px;
		height: 79px;
		color: transparent;
		background: transparent url('../img/intro/intro-view-close.svg') no-repeat;
		background-size: 100% 100%;
		border: 0 none;
		position: absolute;
		top: 45px;
		right: 45px;
		z-index: 1002;
	}
	
	.main {
		display: inline-block;
		width: 100%;
		max-width: 852px;
		vertical-align: middle;
		text-align: left;
		font-size: 12px;
		white-space: normal;
		position: relative;
		z-index: 1001;

		.video-area {
			padding-top: 56.25%;

			iframe {
				width: 100%;
				height: 100%;
				position: absolute;
				top:0;
				left:0;
			}
		}
	}

	@include tab {
		.close {
			width: 70px;
			height: 70px;
		}
	}

	@include mob {
		padding: 0;
		
		.close {
			width: 45px;
			height: 45px;
			top: 20px;
			right: 20px;
		}
	}
}