//
// 팝업 스타일
// --------------------------------------------------

/* 레이어 팝업 - modal */
.layer-pop {
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	padding: 4%;
	text-align: center;
	white-space: nowrap;
	position: fixed;
	top: 0;
	left: 0;
	overflow: auto;
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
	z-index: 1000;

	// 상대적으로 센터 정렬 위한 요소
	&:after {
		content: '';
		display: inline-block;
		width: 0;
		height: 100%;
		vertical-align: middle;
	}

	&:before {
		content: '';
		box-sizing: border-box;
		display: block;
		width: 100%;
		min-width: $site-min-width;
		height: 100%;
		background: rgba(0,0,0,.6);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
	}

	.layer-main {
		box-sizing: border-box;
		display: inline-block;
		width: 100%;
		max-width: 390px;
		padding-top: 69px;
		vertical-align: middle;
		text-align: left;
		white-space: normal;
		background: #222;
		border: 1px solid #222;
		position: relative;
		z-index: 1001;

		&:after {
			content: '';
			display: block;
			width: 100%;
			height: 69px;
			background: url('../img/com/logo-m.png') no-repeat 50% 50%;
			background-size: 112px 44px;
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	.con-body {
		padding: 4%;
		background: #fff;
		position: relative;
	}

	.btn-layer-close {
		box-sizing: border-box;
		width: 69px;
		height: 69px;
		color: transparent;
		background: transparent url('../img/com/ico-header-close.png') no-repeat 50% 50%;
		background-size: 25px 25px;
		border: 0 none;
		position: absolute;
		top: -69px;
		right: 0;
		z-index: 1;
	}
}



/* 공유하기 팝업 - modal */
.layer-share-pop {
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	padding: 4%;
	text-align: center;
	white-space: nowrap;
	position: fixed;
	top: 0;
	left: 0;
	overflow: auto;
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
	z-index: 1000;

	// 상대적으로 센터 정렬 위한 요소
	&:after {
		content: '';
		display: inline-block;
		width: 0;
		height: 100%;
		vertical-align: middle;
	}

	&:before {
		content: '';
		box-sizing: border-box;
		display: block;
		width: 100%;
		min-width: $site-min-width;
		height: 100%;
		background: rgba(0,0,0,.6);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
	}

	.layer-main {
		box-sizing: border-box;
		display: inline-block;
		width: 100%;
		max-width: 300px;
		vertical-align: middle;
		text-align: left;
		white-space: normal;
		background: #fff;
		border: 1px solid #d0d0d0;
		position: relative;
		z-index: 1001;
	}

	.pop-header {
		height: 47px;
		line-height: 47px;
		margin: 0 19px;
		text-align: center;
		font-weight: bold;
		font-size: 18px;
		color: #000;
		border-bottom: 1px solid #d0d0d0;
	}

	.close {
		box-sizing: border-box;
		width: 47px;
		height: 47px;
		color: transparent;
		background: transparent url('../img/com/ico-share-close.png') no-repeat 50% 50%;
		background-size: 16px 16px;
		border: 0 none;
		position: absolute;
		top: 0;
		right: 4px;
		z-index: 1;
	}

	.con-body {
		padding: 19px;
		position: relative;
	}

	.share-list {
		padding-bottom: 3px;
		
		@include clearfix;

		> li {
			float: left;
			width: 33.33%;
			margin-bottom: 15px;
		}

		a {
			display: block;
			text-align: center;
			text-decoration: none;
		}

		.i {
			display: block;
			width: 40px;
			height: 40px;
			margin: 0 auto;
		}

		.t {
			display: block;
			color: #505050;
		}
	}

	.url-copy {
		font-size: 0;
		white-space: nowrap;

		input {
			box-sizing: border-box;
			display: inline-block;
			width: calc(100% - 92px);
			height: 34px;
			line-height: 32px;
			padding: 0 10px;
			font-size: 14px;
			background: #fff;
			border: 1px solid #d0d0d0;
		}

		.btn-url-copy {
			box-sizing: border-box;
			display: inline-block;
			width: 92px;
			height: 34px;
			line-height: 34px;
			text-align: center;
			vertical-align: middle;
			padding: 0;
			text-align: center;
			font-size: 14px;
			color: #fff;
			text-decoration: none;
			white-space: nowrap;
			background: #000;
		}
	}
}