//
// 헤더 스타일
// --------------------------------------------------

/* 공통 헤더 */
.common-header {
	.nav {
		box-sizing: border-box;
		margin: 30px 0 0;
		line-height: 60px;
		text-align: center;
		font-size: 18px;

		a {
			color: #fff;
			text-decoration: none;
			transition: all .4s;
		}

		li.on a,
		a:hover,
		a:focus {
			color: $head-color;
		}
	}
	
	.nav-foot {
		width: 100%;
		text-align: center;
		font-size: 0;
		position: absolute;
		bottom: 27px;
		left: 0;
	}

	.util {
		display: none;
		padding-bottom: 16px;
		font-size: 13px;
		color: #888;

		li {
			display: inline-block;
			position: relative;

			&:not(:first-child) {
				margin-left: 7px;
				padding-left: 8px;

				&:after {
					content: '/';
					display: block;
					position: absolute;
					top: 0;
					left: -2px;
				}
			}
		}
	}

	.sns {
		li {
			display: inline-block;
			
			&:not(:first-child) {
				margin-left: 6px;
			}
		}
	}

	.header-srch-frm {
		display: none;
	}

	// PC large 초과 사이즈일때
	@include pc-large {
		box-sizing: border-box;
		width: 190px;
		height: 100%;
		min-height: 700px;
		background: #2a2a2a;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 100;

		.logo a {
			box-sizing: border-box;
			display: block;
			height: 202px;
			text-align: center;
			color: transparent;
			background: #222 url('../img/com/logo.svg') no-repeat 50% 50%;
		}

		.header-nav-toggle,
		.header-srch-toggle,
		.header-srch-frm {
			display: none;
		}
	}
	
	// PC small 이하 스타일
	@include pc-small {
		$nav-width: 190px;
		$header-height: 70px;

		display: block;
		width: 100%;
		height: $header-height;
		background: #222;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 100;

		.logo {
			padding-top: 12px;
			text-align: center;

			a {
				display: block;
				width: 130px;
				height: 44px;
				margin: 0 auto;
				color: transparent;
				background: #222 url('../img/com/logo-m.png') no-repeat;
				background-size: 130px 44px;
			}
		}

		.header-nav-toggle {
			display: block;
			width: $header-height;
			height: $header-height;
			color: transparent;
			background: transparent url('../img/com/ico-header-menu.png') no-repeat 50% 50%;
			background-size: 30px 23px;
			border: 0 none;
			position: absolute;
			top: 0;
			left: 0;
			transition: all .4s;
		}

		.nav {
			display: none;
			margin: 30px 0 0;
			line-height: 60px;
		}

		.sns {
			display: none;
		}

		.nav-wrap {
			width: $nav-width;
			height: calc(100% - #{$header-height});
			background: #2a2a2a;
			position: fixed;
			top: $header-height;
			left: -$nav-width;
			transition: left .4s;
		}

		.header-srch-toggle {
			display: none;
			box-sizing: border-box;
			width: $header-height;
			height: $header-height;
			color: transparent;
			background: transparent url('../img/com/ico-header-srch.png') no-repeat 50% 50%;
			background-size: 32px 32px;
			border: 0 none;
			position: absolute;
			top: 0;
			right: 0;
			transition: all .4s;
		}
	}

	@include mob {
		.nav {
			width: 100%;
			height: calc(100% - 130px);
			position: absolute;
			top: 0;
			left: 0;
			overflow: auto;
			overflow-x: hidden;
			-webkit-overflow-scrolling: touch;
		}

		.header-srch-toggle {
			display: block;
		}

		.header-srch-frm {
			display: none;
		}
	}
}

/* 메뉴 오픈 되었을때 */
.wrapper.menu-open {
// 	height: 100%;
// 	position: fixed;
// 	overflow: hidden;

	.header-nav-toggle {
		background-color: #2a2a2a;
		background-image: url('../img/com/ico-header-close.png');
		background-size: 25px 25px;
	}

	.common-header {
		.nav-wrap {
			display: block;
			left: 0;
		}

		.nav,
		.sns,
		.util {
			display: block;
		}
	}
}


/* 검색 오픈 되었을때 */
@include mob {
	.wrapper.srch-open {
		.header-srch-toggle {
			background-color: #2a2a2a;
			background-image: url('../img/com/ico-header-close.png');
			background-size: 25px 25px;
		}

		.header-srch-frm {
			display: block;
		}
	}
}